body {
  margin: 0;
  padding: 0;
  /* background: linear-gradient(#43baff, #00d4a3); */
  background: #333;
  font-family: 'Roboto', Helvetica, Arial, sans-serif;
}

input {
  -webkit-appearance: none;
}

h1, h3, label {
  color: #333;
}

h3 {
  margin: 0;
}

h1 {
  margin: 0;
}

p {
  color: #7d7d7d;
  font-weight: 300;
  font-size: 17px;
  padding: 0;
  margin: 0;
}

label {
  margin-top: 20px;
  font-size: 13px;
  font-weight: 400;
  color: #7d7d7d;
  position: relative;
  left: 2px;
}

input, select {
  margin-top: 3px;
  padding: .5em;
  border-radius: 4px;
  border: #cdcdcd 1px solid;
  color: #777;
}

input::-webkit-input-placeholder {
  color: #777;
}

input::-moz-placeholder {
  color: #777;
}

input::-ms-input-placeholder {
  color: #777;
}



button {
  color: #fff;
  border: 1px solid #4b742f;
  background-color: #6ba442;
  background-image: -webkit-gradient(linear,left top, left bottom,color-stop(0, #6ba442),to(#568436));
  background-image: -webkit-linear-gradient(top,#6ba442 0,#568436 100%);
  background-image: -o-linear-gradient(top,#6ba442 0,#568436 100%);
  background-image: linear-gradient(-180deg,#6ba442 0,#568436 100%);
  -webkit-box-shadow: inset 0 -1px 0 0 rgba(0,0,0,.15);
          box-shadow: inset 0 -1px 0 0 rgba(0,0,0,.15);
  border-radius: 5px;
  padding: 7px;
  font-weight: 700;
  font-size: 14px;
  margin-top: 12px;
}

button:hover {
  color: #fff;
  border-color: #3f6228;
  background-color: #5f923b;
  background-image: -webkit-gradient(linear,left top, left bottom,color-stop(0, #659b3e),to(#4a722e));
  background-image: -webkit-linear-gradient(top,#659b3e 0,#4a722e 100%);
  background-image: -o-linear-gradient(top,#659b3e 0,#4a722e 100%);
  background-image: linear-gradient(-180deg,#659b3e 0,#4a722e 100%);
  -webkit-box-shadow: 0 1px 4px 0 rgba(0,0,0,.1), inset 0 -1px 0 0 rgba(0,0,0,.15);
          box-shadow: 0 1px 4px 0 rgba(0,0,0,.1), inset 0 -1px 0 0 rgba(0,0,0,.15);
  cursor: pointer;
}


